const timerId = setInterval(function() {
  const targetNode = document.getElementById('checkout_different_billing_address_true');
  if (targetNode) {
    clearInterval(timerId);
    const config = {
      attributes: true,
      childList: true,
      subtree: true
    };
    const observeCallback = function(mutationsList, observer) {
      processAfterPay();
    };

    const observer = new MutationObserver(observeCallback);

    observer.observe(targetNode, config);
    processAfterPay();

    document.querySelector('#checkout_billing_address_country').onchange = function() {
      processAfterPay();
    };

    document.querySelector('input#checkout_payment_gateway_35898589248').parentElement.parentElement.onclick = function() {
      document.querySelector('div#payment-gateway-subfields-35898589248').classList.remove('hidden');
    };
  }
}, 1000);

const showAfterPay = function() {
  document.querySelector('input#checkout_payment_gateway_35898589248').parentElement.parentElement.classList.remove('hidden');
  document.querySelector('div#payment-gateway-subfields-35898589248').classList.add('hidden');
}

const removeAfterPay = function() {
  if (document.querySelector('input#checkout_payment_gateway_35898589248').checked === true) {
    document.querySelector('input#checkout_payment_gateway_35898589248').checked = false;
  }
  document.querySelector('input#checkout_payment_gateway_35898589248').parentElement.parentElement.classList.add('hidden');
  document.querySelector('div#payment-gateway-subfields-35898589248').classList.add('hidden');
}

const processAfterPay = function() {
  var isChecked = (document.querySelector('input#checkout_different_billing_address_true').checked === true);
  var isUS = (document.querySelector('select#checkout_billing_address_country').value === 'United States' ? true : false);
  var isContainUS = (Array.from(document.querySelectorAll('address')).some((addressElem) => addressElem.innerText.includes('United States')) === true);

  if (isChecked) {
    if (isUS) {
      showAfterPay();
    } else {
      removeAfterPay();
    }
  } else {
    if (isContainUS) {
      showAfterPay();
    } else {
      removeAfterPay();
    }
  }
}
